import styled from 'styled-components';

import { simpleFlex } from '../../styles/mixins';

export const Container = styled.div`
  
`;

export const Info = styled.div`
  ${simpleFlex}
  img {
    width: 30px;
  }

  p {
    margin-left: 10px;
    font-size: 14px;
    font-weight: bold;
  }
`;

export const ActionsContainer = styled.div`
  ${simpleFlex}
  justify-content: space-between;
  margin-top: 50px;

  div {
    flex: 1;
  }
`;

export const ButtonReset = styled.button`
  cursor: pointer;
  border: 0;
  background-color: #058586;
  color: white;
  padding: 8px 30px;
  border-radius: 4px;
  font-size: 18px;
  -webkit-transition: 0.4s ease all;
  transition: 0.4s ease all;
  width: 90%;

  
  &:hover {
    opacity: 0.8;
  }
`;

export const ButtonImport = styled.button`
  cursor: pointer;
  border: 0;
  background-color: #122e41;
  color: white;
  padding: 8px 30px;
  border-radius: 4px;
  font-size: 18px;
  -webkit-transition: 0.4s ease all;
  transition: 0.4s ease all;
  width: 90%;

  
  &:hover {
    opacity: 0.8;
  }
`;

export const LoadingContainer = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;

  p {
    margin-top: 30px;
  }
`;

export const Error = styled.div`
  margin-top: 50px;
  color: red;
  font-size: 18px;
  font-weight: bold;
`;